.wrap {
    display: flex;
    width: 100%;
    height: 100%;
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.4);
}

.overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    transition: all 0.5s ease-in-out;
}

.modal {
    background-color: #fff;
    width: 70%;
    max-width: 500px;
    padding: 40px 30px;
    border-radius: 10px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    box-shadow: 0 3px 5px rgba(0, 0, 0, 0.1);
    transition: all 0.5s ease-in-out;

    img {
        border-radius: 5px;
        width: 100%;
    }

    svg {
        position: absolute;
        right: 10px;
        top: 10px;
        height: 24px;
        width: 24px;
        opacity: 0.3;
        cursor: pointer;

        @media (min-width: 767px) {
            &:hover {
                opacity: 1;
            }
        }
    }
}
.title {
    margin-bottom: 15px;
    text-align: center;
}
.form {
    display: flex;
    flex-direction: column;
    align-items: center;
    row-gap: 10px;
    input {
        padding: 10px 20px;
        border: 1px solid rgba(0, 0, 0, 0.2);
        border-radius: 10px;
        width: 100%;
        font-size: 16px;
        transition: all 0.2s ease-in;
    }
    input:last-child {
        border-radius: 4px;
        max-width: 200px;

        @media (min-width: 767px) {
            &:hover {
                background-color: rgb(100, 7, 7);
            }
        }
    }
    input:focus {
        border: 1px solid rgba(170, 53, 53, 0.6);
    }
    b {
        font-size: 24px;
    }
}
.overlay.animated {
    opacity: 0;
    visibility: hidden;

    .modal {
        opacity: 0;
        visibility: hidden;
        transform: translate(-50%, -30%);
        transition-delay: 0.2s;
    }

    &.show {
        opacity: 1;
        visibility: visible;

        .modal {
            opacity: 1;
            visibility: visible;
            transform: translate(-50%, -50%);
        }
    }
}
.total {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    margin-top: 20px;
    @media (max-width: 768px) {
        flex-direction: column;
        row-gap: 20px;
        align-items: center;
        margin-top: 10px;
    }

    span {
        font-size: 20px;
    }

    input {
        border-radius: 4px;
        max-width: 200px;

        @media (min-width: 767px) {
            &:hover {
                background-color: rgb(100, 7, 7);
            }
        }
    }
}
